<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#FFA70F"
      menu-bg-color="#FB940C"
      color="#FFFFFF"
      title="常见问题"
      :show-back-button="true"
      :on-back-button-click="back"
      :animate="false"
    />

    <div class="page-content rebate-ask-for-help" style="padding-top: 70px;">
      <!-- 常见问题 -->
      <accordion :items="questions" :active-index="activeIndex"></accordion>
    </div>
  </div>
</template>
<style lang="scss">
  .rebate-ask-for-help {
    background: #FFF !important;

    .v-accordion {
      div:first-child > div:first-child > div:first-child {
        visibility: hidden;
      }
    }
  }

  .hairline-top::before {
    visibility: hidden;
  }

  .hairline-bottom::after {
    visibility: hidden;
  }
</style>

<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import Accordion from '@/widget/Accordion.vue'
  import {QUESTIONS} from './constants'

  export default {
    components: {
      AnimateHeader,
      Accordion
    },

    data () {
      return {
        questions: QUESTIONS,
        questionIndex: 0,
        activeIndex: -1
      }
    },

    methods: {
      back () {
        let backRoute = this.$route.name.split('_')[0]
        $router.back('/' + backRoute)
      }
    }
  }
</script>



