export const QUESTIONS = [
  {
    title: '集分宝多久到账 ？',
    content: `
      对于符合条件的有效订单，集分宝将在确认收货的15个自然日后自动发放至您的账户
    `
  },
  {
    title: '如何提现集分宝 ？',
    content: '请从钱咖首页的右上角进入「个人中心」提现'
  },
  {
    title: '哪些情况没返利 ？',
    content: `
      1.聚划算<br/>
      2.虚拟商品（虚拟商品是无实物性质，网上发布时默认无法选择物流运输的商品）<br/>
      3.运费、优惠券、淘金币抵扣部分<br/>
      4.交易成功后退款的订单<br/>
      5.货到付款的订单
    `
  },
  {
    title: '确认收货后仍看不到订单 ？',
    content: '确认收货后，在1小时内可以看到订单，如果超时未看到订单，请导入订单，通过提交淘宝订单号，即可找到订单'
  }
]



